import { useQuery } from "@tanstack/react-query";
import {
  getAllFeaturedCategories,
  getAllFeaturedProductTypes,
  getProductTypesBasedOnBrand,
} from "../api/categoryApis";

const useAllFeaturedCategories = (options) => {
  return useQuery({
    queryKey: ["categories", "featured"],
    queryFn: getAllFeaturedCategories,
    staleTime: 1000 * 60 * 5,
    ...options,
  });
};

const useAllFeaturedProductTypes = () => {
  return useQuery({
    queryKey: ["productTypes", "featured"],
    queryFn: getAllFeaturedProductTypes,
    staleTime: 1000 * 60 * 5,
    placeholderData: [],
  });
};

const useProductTypesBasedOnBrand = (brandId) => {
  return useQuery({
    queryKey: ["productTypes", brandId],
    queryFn: () => getProductTypesBasedOnBrand(brandId),
    staleTime: 1000 * 60 * 5,
    placeholderData: [],
    enabled: !!brandId,
  });
};

export {
  useAllFeaturedCategories,
  useAllFeaturedProductTypes,
  useProductTypesBasedOnBrand,
};
