import { Link, useParams } from "react-router-dom";
import Carousel from "../../components/Carousel";
import useMediaQuery from "../../hooks/useMediaQuery";
import { useProductTypesBasedOnBrand } from "../../services/categoryServices";

const ProductTypeList = () => {
  const { brandId } = useParams();
  const { data: featuredProductTypes, isLoading } =
    useProductTypesBasedOnBrand(brandId);
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const maxLg = useMediaQuery("(max-width: 1024px)");

  return (
    !isLoading &&
    featuredProductTypes?.length > 0 && (
      <div className="container max-w-full space-y-6 max-lg:space-y-4 relative py-4">
        <div className="max-w-max mx-auto">
          <Carousel
            slideWidth={maxLg ? 80 : maxXl ? 100 : 120}
            spaceBetween={maxLg ? 10 : 30}
            allowTouchMove
          >
            {featuredProductTypes.map((subcategory) => (
              <Link
                key={subcategory._id}
                to={`/product-type/${subcategory._id}`}
                className="space-y-2 inline-block relative"
              >
                <img
                  src={subcategory.logoUrl}
                  alt={subcategory.name || subcategory._id}
                  loading="lazy"
                  className="size-[120px] max-xl:size-[100px] max-lg:size-[80px] rounded-full object-cover object-center"
                />
                <p className="text-sm max-xl:text-xs max-lg:text-xxs text-center leading-none">
                  {subcategory.name}
                </p>
              </Link>
            ))}
          </Carousel>
        </div>
        {/* <div className="absolute inset-0 -z-10 bg-roseYellow bg-opacity-50 blur-3xl" /> */}
      </div>
    )
  );
};

export default ProductTypeList;
